import React from 'react';
import agent from '../../../agent';
import { connect } from 'react-redux';
import Menu from 'material-ui/Menu';
import MenuItem from 'material-ui/MenuItem';

import './styles.css';

const mapStateToProps = state => ({
  resume : state.resume,
  log : console.log('explorer state',state)
});

const mapDispatchToProps = dispatch => ({
    onJobClick: (event, value) => {
        console.log(event.target)
        var action = { type: 'JOB_SELECTED', value };
        console.log('explorer: in "onJobClick" mapping',action);
        return dispatch(action);
    }
});

class Explorer extends React.Component {
  componentWillMount() {
    console.log('explorer props',this.props);
  }

  render() {
    var jobs = [];
    if(this.props.resume && this.props.resume.json && this.props.resume.json.jobs) {
      this.props.resume.json.jobs.map(
        (job, idx) => {
          console.log('processing job',job,idx);
          jobs.push(
            <MenuItem className  = "job"
                      key        = {idx}
                      value      = {idx}
                      >
                <div className="label">
                    <div className="name">{job.name}</div>
                    <div className="dates">
                        {job.dates.start} - {job.dates.end}
                    </div>
                </div>
            </MenuItem>
          )
        }
      )
    }
    console.log('explorer: jobs = ',jobs);
    return (
        <div className="explorer">
            <Menu onChange={this.props.onJobClick}>
                <MenuItem value={-1}>Summary</MenuItem>
                {jobs}
            </Menu>
        </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Explorer);



