import Banner from '../Home/Banner';
import React from 'react';
import agent from '../../agent';
import { connect } from 'react-redux';
import Drawer from 'material-ui/Drawer';
import TagList from './TagList';
import JobContent from './JobContent';
import Explorer from './Explorer';
import './styles.css';

const mapStateToProps = state => ({
  appName: state.common.appName,
  resume : state.resume,
  log : console.log('resume state',state)
});

const mapDispatchToProps = dispatch => ({
  onLoad: (payload) => {
        var action = { type: 'RESUME_DATA_LOADED', payload };
        console.log('in "onLoad" mapping',action);
        return dispatch(action);
    }
});

class Resume extends React.Component {
  componentWillMount() {
    this.props.onLoad(agent.Resume.profile());
    console.log('resume props',this.props);
  }

  render() {
    var tags = [];
          // <div className="explorer">
          // </div>
    console.log(this.props.resume);
    //  eventually let's NOT do this if we have a job selected.  It's extra processing that's not necessary
    var intro = this.props.resume.json?this.props.resume.json.summary.intro:null;
    var introText = intro?intro.split('\n'):[];
    var introContent = introText.map(
      (paragraph, idx) => {
        return <div key={idx}>{paragraph}</div>
      }
    );
    //  end refactor needed
    var jobContent = null;
    if(this.props.resume.selectedJob !== null) {
      jobContent = (<JobContent/>);
    }
    var content = jobContent?jobContent:introContent;
    return (
      <div className="resume-page page">
        <Banner appName={this.props.appName} />
        <div className="resume-container">
          <Explorer />
          <div className="search-bar">
            <TagList />
          </div>
          <div className="content">
            <p>
              select a job in the explorer on the left to view the details of that job here in the content pane
            </p>
            {content}
          </div>
        </div>
      </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Resume);



