import React from 'react';
// import agent from '../../../agent';
import { connect } from 'react-redux';
import Drawer from 'material-ui/Drawer';
import './styles.css';

const mapStateToProps = state => ({
  appName: state.common.appName,
  resume : state.resume,
  log : console.log('JobContent state',state)
});

const mapDispatchToProps = dispatch => ({
  onLoad: (payload) => {
        var action = { type: 'RESUME_DATA_LOADED', payload };
        console.log('in "onLoad" mapping',action);
        return dispatch(action);
    }
});

class JobContent extends React.Component {
  componentWillMount() {
    console.log('JobContent props',this.props);
  }

  render() {
    console.log(this.props.resume);
    //  eventually let's NOT do this if we have a job selected.  It's extra processing that's not necessary
    var jobContent;
    var content = null;
    return (
        <div className="job">
            <p>
                A job is selected
            </p>
        </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(JobContent);



